/*--------------------------------------------------------------
# Modules
--------------------------------------------------------------*/
.ftf-module {
  position: relative;
}
.ftf-module .module__description p:last-child {
  margin-bottom: 0;
}

html.sr .ftf-module-hidden {
  visibility: hidden;
}

/*--------------------------------------------------------------
- Announcement Module
--------------------------------------------------------------*/
.ftf-module-announcement {
  background-size: cover;
}
.ftf-module-announcement__close {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}
.ftf-module-announcement.js-is-sticky-yes {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99999;
}
.admin-bar .ftf-module-announcement.js-is-sticky-yes {
  top: 32px;
}

@media screen and (max-width: 782px) {
  .admin-bar .ftf-module-announcement.js-is-sticky-yes {
    position: absolute;
    top: 46px;
  }
}
/*--------------------------------------------------------------
- Hero Module
--------------------------------------------------------------*/
.ftf-module-hero .ftf-module__title {
  color: inherit;
}
.ftf-module-hero__product-title {
  font-weight: 600;
  padding-bottom: 0.3125rem;
  border-bottom: 1px solid;
}
.ftf-module-hero__video {
  margin: 0;
}
.ftf-module-hero__video-image-wrap {
  position: relative;
}
.ftf-module-hero__video-image {
  position: relative;
  z-index: 1;
  display: block;
}
.ftf-module-hero__video-play-buttton {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 300ms ease-in-out;
}
.ftf-module-hero__video-caption {
  display: block;
  text-align: center;
  margin-top: 1em;
  font-size: 0.875rem;
  opacity: 0.8;
}

/*--------------------------------------------------------------
- CTA Module
--------------------------------------------------------------*/
.ftf-module-cta {
  text-align: center;
}

/*--------------------------------------------------------------
- Testimonials Carousel Module
--------------------------------------------------------------*/
.ftf-module-testimonials-carousel .swiper-slide .row {
  box-sizing: border-box;
}
.ftf-module-testimonials-carousel .testimonial__author {
  font-size: 1.125rem;
}
.ftf-module-testimonials-carousel .testimonial__avatar {
  display: block;
  border-radius: 50%;
}

/*--------------------------------------------------------------
- Gallery Module
--------------------------------------------------------------*/
.ftf-module-gallery__item {
  display: inline-block;
}
.ftf-module-gallery__item-image {
  display: block;
}
.ftf-module-gallery__column {
  margin-bottom: 30px;
}

/*--------------------------------------------------------------
- Accordion Module
--------------------------------------------------------------*/
.ftf-module-accordion__panel {
  border: 1px solid #444;
  margin-bottom: 15px;
}
.ftf-module-accordion__panel-title, .ftf-module-accordion__panel-content {
  margin: 0;
  padding: 15px;
}
.ftf-module-accordion__panel-title {
  cursor: pointer;
  display: flex;
  align-items: center;
}
.ftf-module-accordion__panel-content p:last-child {
  margin: 0;
}
.ftf-module-accordion .ui-accordion-header-icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  position: relative;
  margin-right: 0.9375rem;
}
.ftf-module-accordion .ui-accordion-header-icon:before, .ftf-module-accordion .ui-accordion-header-icon:after {
  content: "";
  width: 1em;
  height: 5px;
  background-color: currentColor;
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.ftf-module-accordion .ui-accordion-header-icon.ftf-module-accordion-icon-default:before {
  transform: translate(-50%, -50%) rotate(90deg);
}